<template>
  <div class="song-list" ref="songList">
    <h2 class="gedanTitle">热门评论</h2>
    <ul >
      <li  class="li-con" v-for="(item, index) in comment" :key="index">
        <img v-lazy="item.user.avatarUrl" :alt="item.nickname">
        <span class="name">{{item.content}}</span>
      </li>
    </ul>
    <div class="loading" v-show="comment.length === 0?1:0">
      <loading></loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import { playlistMixin } from "assets/js/mixin";
import loading from "base/loading/loading";
export default {
  mixins: [playlistMixin],
  components: {
    loading
  },
  props: {
    comment: {
      type: Array
    }
  },
  methods: {
    handlePlaylist(playlist) {
      if (this.$refs.songList) {
        const bottom = playlist.length > 0 ? "60px" : "";

        this.$refs.songList.style.paddingBottom = bottom;
        // this.$refs.toplist.refresh();
        this.$emit("scrollRefresh");
      }
    },

    getDesc(song) {
      return `${song.ar[0].name}·${song.al.name}`;
    },
    getRankCls(index) {
      if (index <= 2) {
        return `icon icon${index}`;
      } else {
        return "text";
      }
    },
    getRankText(index) {
      if (index > 2) {
        return index + 1;
      }
    }
  }
};
</script>

<style scoped lang="scss">
@import "assets/css/mixin.scss";
.gedanTitle {
  width: 100%;
  height: rem(30);
  text-align: left;
  line-height: rem(30);
  color: #ffcd32;
  font-size: rem(15);
  letter-spacing: rem(2);
  margin-top: rem(20);
}
.song-list {
  width: 100%;
  box-sizing: border-box;
  padding: rem(20) rem(30) rem(20) rem(30);
  position: relative;
  background: rgb(34, 34, 34);
}
.li-con {
  width: 100%;
  // height: rem(70);
  // padding: 0 0 0 rem(30);
  box-sizing: border-box;
  display: flex;
  margin: rem(20) 0;
}
.li-con  img {
  width: rem(50);
  height: rem(50);
  line-height: rem(50);
  border-radius: rem(50);
  border-bottom-right-radius: 0;
  vertical-align: middle;
  flex: 0 0 rem(50);
}
.li-con .name {
  display: inline-block;
  font-size: rem(14);
  line-height: rem(30);
  margin-left: rem(20);
  color: hsla(0, 0%, 100%, 0.5);
  vertical-align: middle;
  flex:1;
  margin-top: rem(30);
}
/* 侧面导航字母 */
.tab-right {
  position: absolute;
  z-index: 300;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: rem(20);
  padding: rem(20) 0;
  border-radius: rem(10);
  background: rgba(0, 0, 0, 0.3);
  font-family: Helvetica;
}
.tab-right li {
  padding-bottom: rem(6);
  line-height: 1;
  text-align: center;
  color: hsla(0, 0%, 100%, 0.5);
  font-size: rem(12);
}
#fixed {
  position: absolute;
  top: rem(-1);
  left: 0;
}
/* 子路由进去动画 */
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s;
}
.fade-enter,
.fade-leave-to {
  transform: translate3d(100%, 0, 0);
}
.loading {
  position: fixed;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}
</style>



