<template>
  <div class="app">
    <mvList :title="title" :bg-image="bgImage" :songs="songs" :rank="true" :comment="comment"></mvList>
  </div>

</template>

<script type="text/ecmascript-6">
import { mapGetters } from "vuex";
// import { ERR_OK } from "api/config";
import musicList from "base/musicList/musicList";
import mvList from "base/mvList/mvList";
import { getMusicList, getCommentDetail } from "api/rank";
import { createSong } from "assets/js/song";
const ERR_OK = 200;
export default {
  components: {
    musicList,
    mvList
  },
  computed: {
    title() {
      return this.topList.name;
    },
    bgImage() {
      return this.topList.cover;
    },
    ...mapGetters(["topList"])
  },
  data() {
    return {
      songs: [],
      comment: []
    };
  },
  created() {
    this._getTopList();
    this._getCommentDetail();
  },
  methods: {
    _getCommentDetail() {
      getCommentDetail(this.topList.id).then(res => {
        console.log(res);
        if (res.code === ERR_OK) {
          this.comment = res.hotComments;
        }
      })
    },
    _getTopList() {
      var that = this;
      if (!this.topList.id) {
        this.$router.push("/rank");
        return;
      }
      getMusicList(this.topList.id).then(res => {
        console.log(res);
        if (res.code === ERR_OK) {
          that.songs = res.data;
          // that.songs = that._normalSongs(res.songlist);
        }
      });
    },
    _normalSongs(list) {
      let ret = [];
      list.forEach(item => {
        const musicData = item.data;
        if (musicData.songid && musicData.albummid) {
          // 必须有id和连接码
          ret.push(createSong(musicData));
        }
      });
      return ret;
    }
  }
};
</script>

<style scoped lang="scss">
@import "assets/css/mixin.scss";
.app {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgb(34, 34, 34);
  z-index: 301;
}
</style>



